$shad: 0 1px 4px rgba(0, 0, 0, .3),
-23px 0 20px -23px rgba(0, 0, 0, .6),
23px 0 20px -23px rgba(0, 0, 0, .6),
inset 0 0 40px rgba(0, 0, 0, .1);

.burgerMenuWrapper {

	box-shadow: $shad;
	cursor: pointer;


	&:hover {
		background-color: rgba(0, 0, 0, .1);
	}

	&:active {
		box-shadow: none;
	}

	.burgerMenuImg {
		max-width: 3.5rem;



	}


}


